let lastFrameTime = performance.now();
let frameTimes: number[] = [];
const callbacks: any[] = []
let requestFrameHandle: any = 0;
// 调用业务函数的周期 单位帧
const callbackRunCycle = 10;

let cycleHelper = 0;

export function monitorFPS(callback) {
  callbacks.push(callback);
  const now = performance.now();
  const delta = now - lastFrameTime;
  lastFrameTime = now;

  frameTimes.push(delta);
  
  if (frameTimes.length > 60) { // 保留最近60帧的数据
    frameTimes.shift();
  }
  
  const avgDelta = frameTimes.reduce((a, b) => a + b) / frameTimes.length;
  const currentFPS = 1000 / avgDelta;
  requestFrameHandle = requestAnimationFrame(monitorFPS);
  if (++cycleHelper >= callbackRunCycle) {
    callbacks.forEach(cb => {
      try{
        cb(currentFPS);
      } catch (e) {
  
      }
    });
    cycleHelper = 0;
  }

  return {
    cancel: (cb) => {
      const idx = callbacks.findIndex(v => v === cb);
      if (idx < 0) {
        return;
      }
      callbacks.splice(idx, 1);
      if (callbacks.length === 0) {
        cancelAnimationFrame(requestFrameHandle);
      }
    },
    stop: () => {
      callbacks.length = 0;
      cancelAnimationFrame(requestFrameHandle);
    }
  }
}