import React from "react"
import { AppBar, Toolbar, Typography } from "@mui/material"
import { useTheme } from "@mui/material";
import { Box } from "@mui/material";
import { useEffect } from "react";
import { useState } from "react";
import { monitorFPS } from "../performance/fpsMonitor";
import { useMemo } from "react";
import { getPackageJSON } from "../misc";
import AdvancedLoading from "../toys/advancedLoading";
import { animated, useTransition } from "@react-spring/web";

const BottomBar = () => {

  const theme = useTheme();
  const [fps, setFps] = useState(0);

  useEffect(() => {
    const monitorHandler = _fps => {
      setFps(_fps.toFixed(0));
    };
    const { cancel } = monitorFPS(monitorHandler);
    return () => {
      cancel(monitorHandler);
    }
  }, []);

  const packageJSON = useMemo(() => {
    return getPackageJSON();
  }, []);

  const [squareVisible, setSquareVisible] = useState(false);

  const squareTransition = useTransition(squareVisible, {
    from: { opacity: 0, translateY: 0 },
    enter: { opacity: 1, translateY: -10 },
    leave: { opacity: 0, translateY: 0 },
  });

  return (
    <AppBar
      position="fixed"
      sx={{
        top: 'auto',
        bottom: 0,
        zIndex: theme.zIndex.drawer + 1,
        backgroundColor: 'var(--container-bg-3)'
      }}
    >
      <Toolbar
        style={{
          minHeight: 20
        }}
      >
        <Box
          flex={1}
          position={'relative'}
          display={'flex'}
        >
          <Typography
            onMouseOver={() => setSquareVisible(true)}
            onMouseOut={() => setSquareVisible(false)}
            fontSize={10}
            color="info"

          >{fps} FPS</Typography>
          {squareTransition((style, item) => {
            return item && <animated.div
              style={{
                position: 'absolute',
                top: -60,
                left: -33,
                pointerEvents: 'none',
                ...style,
              }}
            >
              <AdvancedLoading size={10} />
            </animated.div>
          })}
        </Box>
        <Typography
          fontSize={10}
          color="textDisabled"
        >
          {packageJSON.version}
        </Typography>
      </Toolbar>
    </AppBar>
  )
}

export default BottomBar;