import { useShallow } from "zustand/react/shallow"
import { useDataSourceStore } from "./store"
import { Box, List, ListItem, ListItemText, Typography } from "@mui/material";
import React from "react";
import { isProd } from "../misc";
import { Construction } from "@mui/icons-material";


export const DataSourceEntry = () => {
  const { queries } = useDataSourceStore(useShallow(state => ({
    queries: state.queries,
  })));

  if (isProd()) {
    return <Box display={'flex'} alignItems={'center'}>
        <Construction />
        <Typography fontSize={20} fontWeight={800}>WIP</Typography>
      </Box>
  }

  return (
    <Box>
      <List>
        {Object.values(queries).map((query: any) => {
          return (
            <ListItem
              key={query.id}
              secondaryAction={
                <Typography>
                  {query.id}
                </Typography>
              }
            >
              <ListItemText>
                {query.name}
              </ListItemText>
            </ListItem>
          )
        })}
      </List>
    </Box>
  )
}