import { ToggleButton, ToggleButtonGroup } from "@mui/material";
import React, { useMemo } from "react";
import { useCallback } from "react";


const ButtonsWidget = (props) => {
  const { value, onChange, options, ...remain } = props;

  const toggleButtons = useMemo(() => {
    return options.map(option => (
      <ToggleButton 
        key={option.value} 
        value={option.value}
        sx={{
          fontSize: '11px',
          width: '40px',
          height: '30px'
        }}
      >
        {option.label}
      </ToggleButton>
    ))
  }, options);

  const handleChange = useCallback((_, value) => {
    console.log('v', _, value);
    onChange(value);
  }, [onChange]);

  return (
    <ToggleButtonGroup
      value={value}
      exclusive
      onChange={handleChange}
      size="small"
      {...remain}
    >
      {toggleButtons}
    </ToggleButtonGroup>
  )
}

export default ButtonsWidget;